:root {
  --main-color: #77b81e;
  --white: #ffffff;

  --heading-title: 45px;
  --heading-title-mob: 25px;
}
.navbar {
  background-color: #ffffff;
  background-color: var(--white);
}
nav .logo {
  width: 200px;
}

.navbar-light .navbar-nav .nav-link {
  color: black;
  font-size: larger;
}

@media (max-width: 768px) {
  nav .logo {
    width: 165px;
  }
  .navbar-light .navbar-nav .nav-link {
    font-size: medium;
  }

  footer .container .row .logo img {
    width: 200px;
    height: 90px;
  }
}

:root {
  --main-color: #77b81e;
  --white: #ffffff;

  --heading-title: 45px;
  --heading-title-mob: 25px;
}

.logo-slider .item {
  background-color: #fff;
  border-radius: 10px;
  padding: 10px;
}
.logo-slider .item button {
  border: none;
  text-decoration: none;
  text-transform: capitalize;
  color: black;
}
.logo-slider .item img {
  height: 150px;
  border-radius: 10px;
}
.logo-slider p {
  text-transform: uppercase;
  text-align: center;
  padding-top: 10px;
}

.slick-dots li.slick-active button:before {
  color: #77b81e;
  color: var(--main-color);
}
.sliick-dots li button:before {
  font-size: 12px;
}

.slick-next:before,
.slick-prev:before {
  color: #77b81e;
  font-size: 30px;
}

.item:hover {
  display: block;
  transition: all ease 0.3s;
  -webkit-transform: scale(1) translateY(5px);
          transform: scale(1) translateY(5px);
}

@media screen and (max-width: 768px) {
  .slick-next,
  .slick-prev {
    width: 50px;
    margin-right: 15px;
  }
  .logo-slider p {
    font-size: 10px;
  }
  .logo-slider .item {
    background-color: #fff;
    border-radius: 8px;
  }
  .logo-slider .item img {
    height: 100px;
    border-radius: 10px;
  }
}

div .about-content {
  padding: 0 150px;
}
div .about-content p {
  text-align: center;
  letter-spacing: 0.8px;
  line-height: 2.3;
}

div.topimg img{
  height: 50vh;
}
@media screen and (max-width: 768px) {
  div .about-content {
    padding: 0 30px;
  }
  div .about-content p {
    text-align: center;
    letter-spacing: normal;
    line-height: 1.5;
  }
}

